@import url(https://fonts.googleapis.com/css?family=Big+Shoulders+Text:300,400,700&display=swap);
body {
  padding: 0;;
  margin: 0;
  font-family: 'Big Shoulders Text', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  height: 100vh;
  background: url(/static/media/bg.27add3dc.jpg) no-repeat center fixed;
  background-size: cover;
}

.container{
  /* width: 93.9485627837%; */
  margin: 0 3%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media (max-width: 1440px) {
  body{
    font-size: 12px;
  }
}


.header{
  padding-top: 40px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.header form{
  position: relative;
}

.header form:hover,
.header form:focus{
  opacity: 1;
}



.header form input{
  padding: 5px 10px;
  background: rgba(255,255,255,.2);
  border: 1px solid #fff;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #fff;
}

.header form input::-webkit-input-placeholder{
  font-style: italic;
}

.header form input::-moz-placeholder{
  font-style: italic;
}

.header form input:-ms-input-placeholder{
  font-style: italic;
}

.header form input::-ms-input-placeholder{
  font-style: italic;
}

.header form input::placeholder{
  font-style: italic;
}

.header form ul{
  width: 100%;
  max-height: 400px;
  overflow-y: scroll;
  box-sizing: border-box;
  margin: 2px;
  list-style: none;
  position: absolute;
  top: 100%;
  left: 0;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  -webkit-backdrop-filter: blur(3px); /* Use for Safari 9+, Edge 17+ (not a mistake) and iOS Safari 9.2+ */
  backdrop-filter: blur(5px); /* Supported in Chrome 76 */
  background: rgba(199, 199, 199, 0.2);
  color: #fff;
  text-transform: capitalize;
  padding: 0;
  border-radius: 0 0 5px 5px;
}
.header form ul li{
  padding:  2px 5px;
  cursor: pointer;
}
.header form ul li p{
  margin: 0;
}
.header form ul li:hover{
  background: #fff;
  color: #333;
}

.header form button{
  padding: 5px 10px;
  background: #fff url(/static/media/search.3936f3a4.svg) no-repeat center;
  background-size: 60%;
  border: 1px solid #fff;
  border-left: 0;
  border-radius: 0 5px 5px 0;
  width: 25px;
  text-indent: -9999px;
  overflow: hidden;
  cursor: pointer;
  outline: none;
}

.section__infos{
  margin-top: 12em;
  color: #fff;
}

.infos__date{
  color: #ffc31e;
  font-weight: 700;
  text-transform: capitalize;
  margin-bottom: 1em;
}

.infos__location{
  font-size: 6em;
  margin: 0;
  line-height: 1;
}

.infos__location + p{
  margin: 0;
  text-transform: uppercase;
}
.weather{
  margin-top: 10em;
  padding: 50px 3%;
  list-style: none;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  -webkit-backdrop-filter: blur(3px); /* Use for Safari 9+, Edge 17+ (not a mistake) and iOS Safari 9.2+ */
  backdrop-filter: blur(4px); /* Supported in Chrome 76 */
  background: rgba(199, 199, 199, 0.1);
  border-radius: 0 10px 10px 0;
}

.weather__info{
  color: #fff;
  font-weight: 700;
  font-size: 20px;
  position: relative;
  padding: 0 50px 0;
}

.weather__info:first-child{
  padding-left: 0;
}

.weather__info:last-child{
  padding-right: 0;
}

.weather__info:last-child:after{
  display: none;
}

.weather__info::after{
  content: '';
  width: 1px;
  height: 100%;
  background: rgba(255,255,255, .5);
  display: block;
  position: absolute;

  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.weather__info span{
  font-size: 14px;
  font-weight: 300;
  position: relative
}

.weather__info--big{
  font-size: 4em;
  line-height: .4;
  text-align: center;
}

.info__title::before{
  content: '';
  width: 15px;
  height: 15px;
  display: block;
  position: absolute;
  top: 50%;
  left: -20px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.info__title.rain::before{
  background: url(/static/media/drizzle.940323bc.svg) no-repeat center;
  background-size: contain;
}
.info__title.wind::before{
  background: url(/static/media/wind.d5793c9e.svg) no-repeat center;
  background-size: contain;
}
.info__title.humidity::before{
  background: url(/static/media/humidity.3c6ec9f3.svg) no-repeat center;
  background-size: contain;
}
.info__title.sunrise::before{
  background: url(/static/media/sun.628eeebd.svg) no-repeat center;
  background-size: contain;
}
.info__title.sunset::before{
  background: url(/static/media/sunset.4c64ae38.svg) no-repeat center;
  background-size: contain;
}

@media (max-width: 599px) {
  .weather{
    margin: 5em 0 0;
    padding: 40px;
    display: flex;
    /* flex-direction: column; */
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-radius: 0;
  }

  .weather__info{
    padding: 10px;
    box-sizing: border-box;
    width: 50%;
    display: block;
    text-align: center;
  }
  .weather__info--big{
    width: 100%;
  }

  .weather__info::after{
    display: none;
  }

}
